import Vue from 'vue'
import store from '../store/index.js'
import Router from 'vue-router'
import Login from '@/components/login'
import Home from '@/components/home/home'
import ClassRoom from '@/components/home/pageview/classroom'
import EditPaper from '@/components/home/pageview/editpaper'

Vue.use(Router)
const getqueryData = function (param) {

  let reg = new RegExp("(^|&)" + param + "=([^&]*)(&|$)");
  let r = window.location.search.substr(1).match(reg);
  if (r != null) {
    return unescape(r[2]);
  } else {
    return undefined;
  }
}
const router = new Router({
  routes: [
    {
      path: '/',
      redirect: '/login',
    },
    {
      path: '/login',
      name: 'login',
      component: Login,

    },
    {
      path: '/home',
      name: 'home',
      redirect: '/home/classroom',
      component: Home,
      children: [

        {
          path: 'classroom',
          name: "classroom",
          component: ClassRoom,
        },
        {
          path: 'editpaper',
          name: "editpaper",
          component: EditPaper,
        },

      ]

    }

  ]
})
router.beforeEach((to, from, next) => {

  if (to.name != 'login') {
    if (sessionStorage.getItem('isLogin')) {
      next()
    } else {
      next({
        path: '/login'
      })
    }
  } else {
    if (getqueryData("code")) {
      alert("获取code:" + getqueryData('code'))
      alert("获取来源:" + getqueryData('type'))
      Vue.prototype.$http({
        url: "/user/login",
        data: { unionid: code, type: 2, platform: getqueryData('type') == 'wx' ? 1 : 2 }
      })
        .then(({ res, ok }) => {
          if (ok) {
            store.commit('saveLoginInfo', {
              isLogin: true,
              phone: res.phone,
              type: res.type,
              name: res.name
            })
            next('/home')
          } else {
            alert(res);
            window.location.href = window.location.href.substring(0, window.location.href.indexOf('?') - 1)
          }
        })
        .catch(error => {
          console.log(error);
        });

    } else {
      next()
    }

  }
})

export default router



